<template>
  <span v-if="IsLogin && !IsFirstLogin">
    <v-card class="mx-auto">
      <v-navigation-drawer
        app
        fixed
        clipped
        v-if="drawer"
        permanent
        width="200"
      >
        <v-list dense auto>
          <v-list-item
            v-for="item in menu"
            :to="item.path"
            :key="item.path"
            active-class="primary white--text"
          >
            <v-list-item-content>
              <div :style="{ dislay: `flex` }">
                <v-icon :style="{ fontSize: '18px', margin: `0px 12px` }">{{
                  item.icon
                }}</v-icon>
                <span>{{ item.title }}</span>
              </div>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-navigation-drawer>
    </v-card>
  </span>
</template>

<script>
export default {
  props: {
    drawer: Boolean,
    showMenu: Function
  },
  computed: {
    menu() {
      let menu = [];

      if (
        this.fnCheckMission(this.Session, [{ m: this.M_ROLE, p: this.P_READ }])
      ) {
        menu.push({
          title: this.$t("menu.role"),
          path: "/role",
          icon: "mdi-apple-keyboard-command"
        });
      }
      if (
        this.fnCheckMission(this.Session, [
          { m: this.M_ACCOUNT, p: this.P_READ }
        ])
      ) {
        menu.push({
          title: this.$t("menu.account"),
          path: "/account",
          icon: "mdi-contacts"
        });
      }
      if (
        this.fnCheckMission(this.Session, [{ m: this.M_TEAM, p: this.P_READ }])
      ) {
        menu.push({
          title: this.$t("menu.team"),
          path: "/team",
          icon: "mdi-human-male-female"
        });
      }

      if (
        this.fnCheckMission(this.Session, [
          { m: this.M_BUSINESS, p: this.P_READ }
        ])
      ) {
        menu.push({
          title: this.$t("menu.business"),
          path: "/business",
          icon: "mdi-account-switch"
        });
      }
      if (
        this.fnCheckMission(this.Session, [
          { m: this.M_PRODUCT, p: this.P_READ }
        ])
      ) {
        menu.push({
          title: this.$t("menu.product"),
          path: "/product",
          icon: "mdi-database"
        });
      }

      if (
        this.fnCheckMission(this.Session, [{ m: this.M_LEAD, p: this.P_READ }])
      ) {
        menu.push({
          title: this.$t("menu.lead"),
          path: "/lead",
          icon: "mdi-pen"
        });
      }
      if (
        this.fnCheckMission(this.Session, [
          { m: this.M_CAMPAIN, p: this.P_READ }
        ])
      ) {
        menu.push({
          title: this.$t("menu.campaign"),
          path: "/campaign",
          icon: "mdi-launch"
        });
      }

      if (
        this.fnCheckMission(this.Session, [{ m: this.M_APP, p: this.P_READ }])
      ) {
        menu.push({
          title: this.$t("menu.application"),
          path: "/application",
          icon: "mdi-apps"
        });
      }

      return menu;
    }
  }
};
</script>
